/** SOME CHANGES TO START THE STYLE **/
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem; }

p {
  font-size: 2rem;
  color: #000000; }

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1, h2, h3 {
  margin: 0; }

h1 {
  font-size: 4rem;
  font-weight: 900; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem; }

.bg-orange {
  background-color: #ff6a3d; }

.bg-acua {
  background-color: #9daaf2; }

.header {
  background-color: #1a2238; }
  .header .header-content {
    padding: 2.5rem 0; }
    @media (min-width: 768px) {
      .header .header-content {
        display: flex;
        justify-content: space-between;
        align-items: center; } }
  .header h1 {
    color: #ffffff;
    margin-bottom: 1rem;
    text-align: center; }
    @media (min-width: 768px) {
      .header h1 {
        margin-bottom: 0; } }
  .header.fix {
    position: fixed;
    width: 100%;
    box-shadow: 0 0.3rem 0.3rem #1a2238; }

.main-navigation {
  display: flex;
  gap: 2rem;
  justify-content: center; }
  .main-navigation a {
    color: #ffffff;
    font-size: 2rem; }
    .main-navigation a:hover {
      color: #f4db7d; }

.video {
  position: relative;
  max-height: 50rem;
  overflow: hidden; }
  .video .video__overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#1a2238+0,ffffff+100&1+0,0.5+100 */
    background: -moz-linear-gradient(top, #1a2238 0%, rgba(255, 255, 255, 0.5) 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(top, #1a2238 0%, rgba(255, 255, 255, 0.5) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, #1a2238 0%, rgba(255, 255, 255, 0.5) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1a2238', endColorstr='#80ffffff',GradientType=0 );
    /* IE6-9 */ }
  .video video {
    display: block;
    width: 100%; }

.content__video {
  display: flex;
  align-items: center;
  height: 100%;
  padding-left: 10rem; }
  .content__video h2 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: #ffffff;
    font-size: 6rem; }
    .content__video h2 span {
      font-size: 2.5rem;
      border-radius: .5rem;
      padding: 0 .5rem; }

.about-festival {
  padding: 5rem 0; }
  @media (min-width: 768px) {
    .about-festival {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 5rem; } }

.about-festival__image--image {
  border-radius: .5rem; }

.about-festival__content {
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .about-festival__content h2 {
    font-size: 4rem;
    line-height: 2; }
  .about-festival__content .about-festival__content--date {
    color: #1a2238;
    font-weight: 700; }
  .about-festival__content .about-festival__content--text {
    line-height: 1.5; }

.lineup {
  background-color: #1a2238;
  padding: 3rem; }
  .lineup h3, .lineup p {
    text-align: center;
    color: #ffffff; }
  .lineup .day {
    text-transform: uppercase;
    padding: 1.5rem; }

@media (min-width: 768px) {
  .scenaries-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 3rem; } }

.scenaries-container .scenary-name {
  color: #000000;
  font-size: 2rem;
  font-weight: 300; }
  .scenaries-container .scenary-name:after {
    content: '';
    width: 15rem;
    height: 10rem;
    display: block;
    margin: .5rem auto 0 auto;
    background-size: 15rem;
    background-repeat: no-repeat;
    background-position: center; }

.scenary {
  border-radius: .5rem; }

.edm .scenary-name:after {
  background-image: url(/minimg/icono_edm.png); }

.rock .scenary-name:after {
  background-image: url(/minimg/icono_rock.png); }

.claendary {
  padding: 0;
  list-style: none; }
  .claendary li {
    padding: 1.5rem;
    font-weight: 700; }
    .claendary li span {
      font-weight: 400; }

.bg-orange .claendary li:nth-child(odd) {
  background-color: #ff5624; }

.bg-acua .claendary li:nth-child(odd) {
  background-color: #8696ef; }

.galery {
  padding: 3rem 0;
  text-align: center; }

.galery__images {
  padding: 0;
  list-style: none;
  margin-top: 3rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 3rem; }
  @media (min-width: 768px) {
    .galery__images {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      column-gap: 2.5rem; } }
  @media (min-width: 1200px) {
    .galery__images {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      column-gap: 2.5rem; } }

.image__edit {
  border-radius: .5rem; }

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center; }
  .overlay img {
    width: 45%;
    border-radius: .5rem; }

.close-btn {
  width: 4rem;
  height: 4rem;
  background-color: #1a2238;
  display: grid;
  place-items: center;
  color: #ffffff;
  font-size: 2.5rem;
  border-radius: 50%;
  border: 2px solid #000000; }
  .close-btn:hover {
    cursor: pointer; }

.class-body {
  overflow: hidden; }

.tickets {
  padding: 3rem 0; }
  .tickets h3 {
    text-align: center;
    color: #ffffff; }

.tickets__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 10rem; }

.plan p {
  color: #ffffff;
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 1.5rem; }

.tickets__table {
  width: 100%;
  border-spacing: 0;
  border-collapse: separate; }
  .tickets__table thead {
    background-color: #1a2238; }
    .tickets__table thead th {
      padding: 1.5rem;
      color: #ffffff;
      text-transform: uppercase; }
  .tickets__table tbody {
    background-color: #ffffff;
    text-align: center; }
    .tickets__table tbody td {
      padding: 1rem;
      border-bottom: 1px solid #e1e1e1; }
    .tickets__table tbody tr:last-of-type td {
      border: none; }
  .tickets__table tfoot {
    background-color: #9daaf2;
    text-align: center; }
    .tickets__table tfoot td {
      padding: 1rem;
      color: #000000;
      font-weight: bold; }

.footer {
  background-color: #1a2238;
  padding: .5rem;
  text-align: center; }

.p {
  font-size: 2rem;
  margin: 1rem;
  color: #ffffff; }
  .p span {
    font-weight: bold; }

.p1 {
  font-size: 1.5rem;
  color: #ffffff; }
